<template>
  <div class="my-2 mx-2">
    <div class="flex justify-between">
      <Label :for="name">
        {{ label }}
      </Label>

      <Label
        class="text-sm text-gray-400"
        v-if="required"
      >
        Required
      </Label>
    </div>

    <div class="mt-1">
      <input
        class="w-full border-gray-300 rounded-md shadow-sm placeholder-gray-400 focus:ring-orange-500 focus:border-orange-500 sm:text-sm"
        :type="type"
        :name="name"
        v-model="inputValue"
        :placeholder="placeholder"
      />
    </div>
    
    <InputError :errors="errors" />
  </div>
</template>

<script setup lang="ts">
import {computed} from 'vue'
import Label from '@/components/Form/Label.vue'
import InputError from '@/components/Feedback/InputError.vue'

const props = defineProps({
  label: String,
  required: Boolean,
  placeholder: String | Number,
  modelValue: String | Number,
  errors: String,
  type: {
    type: String,
    default: 'text',
  },
})

const emit = defineEmits(['update:modelValue'])

const name = computed(() => props.label.toLowerCase())

const inputValue = computed({
  get() {
    return props.modelValue
  },
  set(value) {
    emit('update:modelValue', value)
  },
})
</script>
